import React, { useState, useEffect, useRef } from 'react';
import { Row, Col, Divider, Typography, DatePicker, Button } from 'antd';
import IsoWidgetsWrapper from './widgets-wrapper';
import ShortWidget from './shortWidget/sale-widget';
import LongWidget from './longWidget/sale-widget';
import basicStyle from '../../settings/basicStyle';
import IntlMessages from '@isomorphic/shared/isomorphic/components/utility/intlMessages';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import LayoutContentWrapper from '@isomorphic/shared/isomorphic/components/utility/layoutWrapper';
import moment from 'moment';
import XLSX from 'xlsx';
//import * as saveSvgAsPng from 'save-svg-as-png';
import html2canvas from 'html2canvas';
import { Spin } from 'antd';
export default function Report() {
  //well, I tryed to put all loading in one variable. To save time, just do it seperately. If you can, please put them together
  //const [loading, setLoading] = useState(Array(7).fill(true));
  const [loading1, setLoading1] = useState(true);
  const [loading2, setLoading2] = useState(true);
  const [loading3, setLoading3] = useState(true);
  const [loading4, setLoading4] = useState(true);
  const [loading5, setLoading5] = useState(true);
  const [loading6, setLoading6] = useState(true);

  const [turnaround, setTurnaround] = useState([]);
  const [perday, setPerday] = useState([]);
  const [approvedByPolicy, setApprovedByPolicy] = useState([]);
  const [approvedByValue, setApprovedByValue] = useState([]);
  const [claimCount, setClaimCount] = useState([]);
  const [usdApproved, setUsdApproved] = useState([]);
  const [usdRejected, setUsdRejected] = useState([]);
  const [chartDate, setChartDate] = useState([
    moment()
      .subtract(1, 'months')
      .format(),
    moment().format(),
  ]);
  //turnaround perday
  useEffect(() => {
    setLoading1(true);
    SuperFetch.get('report/turnaroundTime', chartDate)
      .then(response => {
        if (response.status === 200) {
          const average =
            response.data.reduce(
              (acc, curr) =>
                (acc += moment(curr.updatedAt).diff(
                  moment(curr.createdAt),
                  'days'
                )),
              0
            ) / response.data.length;
          const perDay = response.data.length / 90;

          setTurnaround([
            ['Label', 'Value'],
            ['Turnaround', average],
          ]);
          setPerday([
            ['Label', 'Value'],
            ['Per Day', perDay],
          ]);
          setLoading1(false);
        } else {
          notification('error', response.error.message);
        }
      })
      .catch(e => console.log(e));
  }, [chartDate]);

  //approved by policy
  useEffect(() => {
    setLoading2(true);
    SuperFetch.get('report/approvedClaimedCountByType', chartDate)
      .then(response => {
        if (response.status === 200) {
          const data = response.data.map(item => [
            item.policyType,
            item.policyTypeCount,
          ]);
          data.unshift(['Type', 'Count']);
          setApprovedByPolicy(data);
          setLoading2(false);
        } else {
          notification('error', 'Something wrong in retrieving CSV');
        }
      })
      .catch(e => console.log(e));
  }, [chartDate]);

  //approved by value
  useEffect(() => {
    setLoading3(true);
    SuperFetch.get('report/USDApproved', chartDate)
      .then(response => {
        if (response.status === 200) {
          var table = response.data.map(claim => {
            return {
              approved: claim.ClaimInfoVisits.reduce((acc, curr) => {
                //Get total approved from each visit
                var billingApprovedTotal = curr.BillingInfos.reduce(
                  (accBill, currBill) => accBill + currBill.approved,
                  0
                );
                //Get acc and convert to USD
                acc += billingApprovedTotal / curr.billingRate;
                return acc;
              }, 0),
            };
          });
          const total = table.length;
          const greater = table.filter(item => item.approved > 250).length;

          var data = [];
          data.push(['Amount', 'Count']);
          data.push(['greater or equal then $250', total - greater]);
          data.push(['less than $250', greater]);
          setApprovedByValue(data);
          setLoading3(false);
        } else {
          notification('error', 'Something wrong in retrieving CSV');
        }
      })
      .catch(e => console.log(e));
  }, [chartDate]);

  //claim Count
  useEffect(() => {
    setLoading4(true);
    SuperFetch.get('report/claimCount', chartDate)
      .then(response => {
        if (response.status === 200) {
          var table = response.data.map(item => [
            moment(item.month).format('MM-YYYY'),
            item.prCount,
            item.cpCount,
          ]);
          table.unshift(['Month', 'pr Count', 'cp Count']);

          setClaimCount(table);
          setLoading4(false);
        } else {
          notification('error', 'Something wrong in retrieving CSV');
        }
      })
      .catch(e => console.log(e));
  }, [chartDate]);

  //USD Approved
  useEffect(() => {
    setLoading5(true);
    SuperFetch.get('report/USDApproved', chartDate)
      .then(response => {
        if (response.status === 200) {
          var table = response.data.map(claim => {
            return {
              month: moment(claim.createdAt).format('MM-YYYY'),
              approved: claim.ClaimInfoVisits.reduce((acc, curr) => {
                //Get total approved from each visit
                var billingApprovedTotal = curr.BillingInfos.reduce(
                  (accBill, currBill) => accBill + currBill.approved,
                  0
                );
                //Get acc and convert to USD
                acc += billingApprovedTotal / curr.billingRate;
                return acc;
              }, 0),
            };
          });
          var tableByMonth = table.reduce((acc, obj) => {
            var index = acc.findIndex(element => element[0] === obj.month);
            if (index < 0) {
              acc.push([obj.month, obj.approved]);
            } else {
              acc[index][1] += obj.approved;
            }

            return acc;
          }, []);
          tableByMonth.unshift(['Month', 'USD total']);
          setUsdApproved(tableByMonth);
          setLoading5(false);
        } else {
          notification('error', 'Something wrong in retrieving CSV');
        }
      })
      .catch(e => console.log(e));
  }, [chartDate]);

  //USD rejected
  useEffect(() => {
    setLoading6(true);
    SuperFetch.get('report/USDRejected', chartDate)
      .then(response => {
        if (response.status === 200) {
          var table = response.data.map(claim => {
            return {
              month: moment(claim.createdAt).format('MM-YYYY'),
              rejected: claim.ClaimInfoVisits.reduce((acc, curr) => {
                //Get total approved from each visit
                var billingRejectedTotal = curr.BillingInfos.reduce(
                  (accBill, currBill) =>
                    accBill + (currBill.value - currBill.approved),
                  0
                );
                //Get acc and convert to USD
                acc += billingRejectedTotal / curr.billingRate;
                return acc;
              }, 0),
            };
          });

          var tableByMonth = table.reduce((acc, obj) => {
            var index = acc.findIndex(element => element[0] === obj.month);
            if (index < 0) {
              acc.push([obj.month, obj.rejected]);
            } else {
              acc[index][1] += obj.rejected;
            }

            return acc;
          }, []);
          tableByMonth.unshift(['Month', 'USD total']);

          setUsdRejected(tableByMonth);
          setLoading6(false);
        } else {
          notification('error', 'Something wrong in retrieving CSV');
        }
      })
      .catch(e => console.log(e));
  }, [chartDate]);

  const inputEl = useRef(null);
  const datePickerClaim = useRef(null);
  const datePickerPending = useRef(null);
  const onButtonClickChart = () => {
    setChartDate(inputEl.current.picker.state.value.map(item => item.format()));
  };
  const onButtonClickClaim = () => {
    //console.log(datePickerClaim.current.picker.state.value.map(item => item.format()));
    let param = datePickerClaim.current.picker.state.value.map(item =>
      item.format()
    );
    param.push('claims');
    SuperFetch.get('report/csv', param)
      .then(response => {
        console.log('response', response);
        if (response.status === 200) {
          var ws = XLSX.utils.json_to_sheet(response.data);
          var wb = XLSX.utils.book_new();
          XLSX.utils.book_append_sheet(wb, ws, 'report');
          XLSX.writeFile(wb, 'claims report.xlsb');
          //console.log(response)
        } else {
          notification('error', 'Something wrong in retrieving CSV');
        }
      })
      .catch(e => console.log(e));
  };
  const onButtonClickPending = () => {
    let param = datePickerPending.current.picker.state.value.map(item =>
      item.format()
    );
    param.push('pendings');
    SuperFetch.get('report/csv', param)
      .then(response => {
        console.log('response', response);
        if (response.status === 200) {
          var ws = XLSX.utils.json_to_sheet(response.data);
          var wb = XLSX.utils.book_new();
          XLSX.utils.book_append_sheet(wb, ws, 'report');
          XLSX.writeFile(wb, 'pendings report.xlsb');
          //console.log(response)
        } else {
          notification('error', 'Something wrong in retrieving CSV');
        }
      })
      .catch(e => console.log(e));
  };

  const onButtonClickImages = () => {
    //const svgs = document.querySelectorAll("div[aria-label='A chart.']");
    const svgs = document.getElementsByClassName('isoSaleWidget');
    for (let index = 0; index < svgs.length; index++) {
      //saveSvgAsPng.saveSvgAsPng(svgs[index], 'chart.png')
      html2canvas(svgs[index]).then(canvas => {
        //document.body.appendChild(canvas)
        // var reject = document.createElement('img'); //Create <a>
        // console.log(canvas.toDataURL())
        // reject.href = canvas.toDataURL(); //Image Base64 Goes here
        // reject.download = 'chart.png'; //File name Here
        // reject.click();
        var link = document.createElement('a');
        link.href = canvas.toDataURL();
        link.download = 'chart.png';
        link.click();
      });
    }
  };
  const { rowStyle, colStyle } = basicStyle;
  //console.log('heheheh',loading.some(e => e===true))
  console.log('heheheh', loading6 || loading5 || loading5);
  if (loading6 || loading5 || loading4 || loading3 || loading2 || loading1)
    return (
      <div
        style={{
          marginTop: '20%',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <Spin size="large" />
      </div>
    );
  return (
    <LayoutContentWrapper>
      <Row style={rowStyle}>
        <Col>
          <Typography.Title>Reporting</Typography.Title>
        </Col>
        <Divider />
      </Row>
      <Row style={rowStyle} type="flex" justify="space-around">
        <Col>
          <DatePicker.RangePicker
            defaultValue={[moment().subtract(1, 'months'), moment()]}
            format={'DD MMM YYYY'}
            ref={datePickerClaim}
          />

          <Button type="primary" onClick={onButtonClickClaim}>
            EXPORT MONTHLY REPORT
          </Button>
        </Col>
        <Col>
          <DatePicker.RangePicker
            defaultValue={[moment().subtract(1, 'months'), moment()]}
            format={'DD MMM YYYY'}
            ref={datePickerPending}
          />

          <Button type="primary" onClick={onButtonClickPending}>
            EXPORT CURRENT PENDING REPORT
          </Button>
        </Col>
      </Row>
      <div
        style={{
          border: '1px solid white',
          padding: '35px',
          borderRadius: '25px',
        }}
      >
        <Row style={rowStyle} type="flex" justify="space-around">
          <Col>
            <DatePicker.RangePicker
              defaultValue={[moment().subtract(1, 'months'), moment()]}
              format={'DD MMM YYYY'}
              ref={inputEl}
            />

            <Button type="primary" onClick={onButtonClickChart}>
              Submit
            </Button>
          </Col>
          <Col>
            <Button type="primary" onClick={onButtonClickImages}>
              DOWNLOAD IMAGES
            </Button>
          </Col>
        </Row>
        <Row style={rowStyle} gutter={0} justify="start">
          <Col lg={6} md={12} sm={12} xs={24} style={colStyle}>
            <IsoWidgetsWrapper gutterLeft={0}>
              {/* Sale Widget */}
              <ShortWidget
                data={turnaround}
                chartType="Gauge"
                label={<IntlMessages id="widget.salewidget1.label" />}
                title={'TURNAROUND'}
                options={{
                  title: 'turnaround',
                  redFrom: 90,
                  redTo: 100,
                  yellowFrom: 75,
                  yellowTo: 90,
                  minorTicks: 5,
                  forceIFrame: true,
                }}
              />
            </IsoWidgetsWrapper>
          </Col>

          <Col lg={6} md={12} sm={12} xs={24} style={colStyle}>
            <IsoWidgetsWrapper>
              {/* Sale Widget */}
              <ShortWidget
                title={'PROCESSED PER DAY'}
                data={perday}
                chartType="Gauge"
                label={<IntlMessages id="widget.salewidget1.label" />}
                options={{
                  title: 'Per Day',
                  redFrom: 90,
                  redTo: 100,
                  yellowFrom: 75,
                  yellowTo: 90,
                  minorTicks: 5,
                }}
              />
            </IsoWidgetsWrapper>
          </Col>

          <Col lg={6} md={12} sm={12} xs={24} style={colStyle}>
            <IsoWidgetsWrapper>
              {/* Sale Widget */}
              <ShortWidget
                title={'APPROVED BY POLICY'}
                data={approvedByPolicy}
                chartType="PieChart"
                options={{
                  title: 'Approved Claims Count: Split by Type',
                  // Just add this option
                  is3D: false,
                }}
              />
            </IsoWidgetsWrapper>
          </Col>
          <Col lg={6} md={12} sm={12} xs={24} style={colStyle}>
            <IsoWidgetsWrapper>
              {/* Sale Widget */}
              <ShortWidget
                title={'APPROVED BY VALUE'}
                data={approvedByValue}
                chartType="PieChart"
                options={{
                  title: 'Approved Claims Count: Split by Value',
                  // Just add this option
                  is3D: false,
                }}
              />
            </IsoWidgetsWrapper>
          </Col>
        </Row>
        <Row style={rowStyle}>
          {/* Sale Widget */}
          <LongWidget
            data={claimCount}
            chartType="ColumnChart"
            options={{
              title: 'Claims Count',
              chartArea: { width: '30%' },
              hAxis: {
                title: 'Month',
                minValue: 0,
              },
              vAxis: {
                title: 'Count',
              },
            }}
          />
        </Row>
        <Row style={rowStyle}>
          {/* Sale Widget */}
          <LongWidget
            data={usdApproved}
            chartType="ColumnChart"
            options={{
              title: 'USD Approved',
              chartArea: { width: '30%' },
              hAxis: {
                title: 'Month',
                minValue: 0,
              },
              vAxis: {
                title: 'Amount',
                minValue: 0,
              },
            }}
          />
        </Row>
        <Row style={rowStyle}>
          {/* Sale Widget */}
          <LongWidget
            data={usdRejected}
            chartType="ColumnChart"
            options={{
              title: 'USD Reject',
              chartArea: { width: '30%' },
              hAxis: {
                title: 'Month',
                minValue: 0,
              },
              vAxis: {
                title: 'Amount',
                minValue: 0,
              },
            }}
          />
        </Row>
      </div>
    </LayoutContentWrapper>
  );
}
